<style lang="sass" rel="scss" scoped>
    @import '../../static/sass/variables';

    #space {
        perspective: 800;
        -webkit-perspective: 800;
        perspective-origin: 50% 50%;
        -webkit-perspective-origin: 50% 50%;
        background-color: rgba(0, 0, 0, 0.3);
        z-index: 10000;
        position: fixed;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        visibility: hidden;
    }

    .mask{
        -webkit-transform-style:preserve-3d;
        transform-style: preserve-3d;
        position: absolute;
        height: 100%;
        width: 100%;
        left: 0;
        top: 0;
    }

    .modal {
        padding: 50px 60px;
        background-color: #fff;
        border-radius: 4px;
        position: fixed;
        top: 50%;
        margin-top: -300px;
        transition: .5s;
        font-size: 14px;
        visibility: hidden;
        opacity: 0;
        display: flex;
        flex-direction: column;

        input[type="text"], input[type="password"], input[type="email"] {
            width: 100%;
            height: 40px;
            color: #a5a5a5;
            font-size: 12px;
        }

        button {
            height: 40px;
            width: 100%;
            color: #fff;
            border-radius: 3px;
            background-color: $color-blue;

            &:hover {
                background-color: $color-blue-hover;
            }
        }

        >div {
            margin-top: 15px;
        }

        span {
            margin-top: 15px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex: 1;
        }
    }

    .pc-modal {
        width: 500px;
        left: 50%;
        margin-left: -260px;
        height: 460px;
    }

    .m-modal {
        width: 100%;
        height: 430px;

        img {
            height: 70px;
        }
    }
</style>

<template>
    <div id="space" @click="hiddenSign" v-if="!$store.getters.isLogin" ref="space">
        <div class="mask">
            <div :class="[ 'modal', $root.$data.isMobile ? 'm-modal' : 'pc-modal' ]" @click.stop="" ref="signInBox">
                <img src="data:image/jpeg;base64,">
                <div>
                    <input name="username" type="text" v-model="signIn.name" placeholder="邮箱" @keyup.enter="login">
                </div>
                <div>
                    <input name="password" type="password" v-model="signIn.password" placeholder="密码" @keyup.enter="login">
                </div>
                <span>
                    <label>记住我<input type="checkbox" :checked="signIn.remember" v-model="signIn.remember"></label>
                    <em></em>
                </span>
                <div>
                    <button @click="login">登录</button>
                </div>
                <span>
                    <a>忘记密码?></a>
                    <a @click="toRegister">点击注册»</a>
                </span>
            </div>
            <div :class="[ 'modal', $root.$data.isMobile ? 'm-modal' : 'pc-modal' ]" @click.stop="" ref="signUpBox">
                <img src="data:image/jpeg;base64,">
                <div>
                    <input type="text" v-model="signUp.name" placeholder="昵称（2-12个字符组成，1个汉字占2个字符）">
                </div>
                <div>
                    <input type="email" v-model="signUp.email" placeholder="邮箱（填写常用邮箱，用于登录）">
                </div>
                <div>
                    <input type="password" v-model="signUp.password" placeholder="密码（6-16个字符组成，区分大小写）">
                </div>
                <span>
                    <div ref="captcha"></div>
                    <em></em>
                </span>
                <span>
                    <a></a>
                    <a @click="toLogin">已有账号»</a>
                </span>
            </div>
        </div>
    </div>
</template>

<script lang="babel">

    import { setUserInfo } from '../../static/js/storage'

    export default {
        data () {
            return {
                signIn : {
                    remember : true,
                    name : "",
                    password : "",
                    method : ""
                },
                signUp : {
                    captcha : true,
                    name : "",
                    password : "",
                    email : ""
                }
            }
        },
        methods: {
            transforme (ele, rotate) {
                ele.style.transform = "rotateY(" + rotate + "deg)";
                ele.style.webkitTransform = "rotateY(" + rotate + "deg)";
                ele.style.msTransform = "rotateY(" + rotate + "deg)";
                ele.style.mozTransform = "rotateY(" + rotate + "deg)";
                ele.style.oTransform = "rotateY(" + rotate + "deg)";
            },
            showSpace (show) {
                this.$refs.space.style.visibility = show ? "visible" : "hidden";
            },
            hiddenBox (ele) {
                this.transforme(ele, 0);
                ele.style.marginTop = "-300px";
                ele.style.opacity = 0;
                ele.style.visibility = "hidden";
            },
            switchBox (ele, bool) {
                ele.style.visibility = "hidden";
                this.transforme(ele, bool ? 180 : -180);
                ele.style.marginTop = "-200px";
                ele.style.opacity = 0;
            },
            showBox (ele) {
                ele.style.visibility = "visible";
                this.transforme(ele, 0);
                ele.style.marginTop = "-200px";
                ele.style.opacity = 1;
            },
            showLogin () {
                this.showSpace(true);
                this.showBox(this.$refs.signInBox);
                this.switchBox(this.$refs.signUpBox, true);
            },
            showRegister () {
                this.showSpace(true);
                this.showCaptcha();
                this.switchBox(this.$refs.signInBox, false);
                this.showBox(this.$refs.signUpBox);
            },
            hiddenSign () {
                this.showSpace(false);
                this.hiddenBox(this.$refs.signUpBox);
                this.hiddenBox(this.$refs.signInBox);
            },
            toRegister () {
                this.showCaptcha();
                this.switchBox(this.$refs.signInBox, false);
                this.showBox(this.$refs.signUpBox);
            },
            toLogin () {
                this.switchBox(this.$refs.signUpBox, true);
                this.showBox(this.$refs.signInBox);
            },
            login () {
                var regEmail = /^\w+\@\w+\.\w{2,3}$/;
                var regPwd = /^\w{6,16}$/;

                if (this.signIn.name == "") {
                    this.$root.$refs.toast.open({
                        theme: "warning",
                        content: "账号不能为空！"
                    });
                    return
                } else if (!regEmail.test(this.signIn.name)) {
                    this.$root.$refs.toast.open({
                        theme: "warning",
                        content: "账号错误！"
                    });
                    return
                } else {
                    if (regEmail.test(this.signIn.name)) {
                        this.signIn.method = "email";
                    } else {
                        this.signIn.method = "phone";
                    }
                }

                if (this.signIn.password == "") {
                    this.$root.$refs.toast.open({
                        theme: "warning",
                        content: "请输入密码！"
                    });
                    return
                } else if (!regPwd.test(this.signIn.password)) {
                    this.$root.$refs.toast.open({
                        theme: "warning",
                        content: "密码不合法！"
                    });
                    return
                }

                this.$http.post('/door/login', {
                    access : this.signIn.name,
                    secret : this.signIn.password,
                    method : this.signIn.method,
                    remember : this.signIn.remember
                }).then((res) => {
                    this.$root.$refs.toast.open({
                        theme: "success",
                        content: "登录成功！"
                    });
                    setUserInfo(res.body, this.signIn.remember);
                    this.hiddenSign();
                    var bool = true;
                    this.$store.dispatch('setLogin', { bool });
                    document.getElementById('_auth').setAttribute('content', 1);
                    this.$root.$refs.navbar.makeLogin();
                    this.signIn.name = "";
                    this.signIn.password = "";
                    this.signIn.method = "";
                }, (res) => {
                    if (res.status === 422) {
                        this.$root.$refs.toast.open({
                            theme: "warning",
                            content: res.body.message
                        });
                    } else if (res.status === 429) {
                        this.$root.$refs.toast.open({
                            theme: "warning",
                            content: "禁止登录三分钟！"
                        });
                    } else if (res.status === 500) {
                        this.$root.$refs.toast.open({
                            theme: "warning",
                            content: "服务器异常，请稍后再试！"
                        });
                    }
                });
            },
            register () {
                var regName = /^(\w|[\u4e00-\u9fa5])+$/g;
                var regPwd = /^\w{6,16}$/;
                var regEmail = /^\w+\@\w+\.\w{2,3}$/;
                var match = this.signUp.name.replace(/([\u4e00-\u9fa5])/g,'aa').trim().length;
                if (this.signUp.name == "") {
                    this.$root.$refs.toast.open({
                        theme: "warning",
                        content: "请输入昵称！"
                    });
                    return
                } else if (match > 12 || match < 2) {
                    this.$root.$refs.toast.open({
                        theme: "warning",
                        content: "昵称长度不符！"
                    });
                    return
                } else if (!regName.test(this.signUp.name)) {
                    this.$root.$refs.toast.open({
                        theme: "warning",
                        content: "昵称不合法！"
                    });
                    return
                }

                if (this.signUp.password == "") {
                    this.$root.$refs.toast.open({
                        theme: "warning",
                        content: "请输入密码！"
                    });
                    return
                } else if (!regPwd.test(this.signUp.password)) {
                    this.$root.$refs.toast.open({
                        theme: "warning",
                        content: "密码不合法！"
                    });
                    return
                }

                if (this.signUp.email == "") {
                    this.$root.$refs.toast.open({
                        theme: "warning",
                        content: "请输入邮箱！"
                    });
                    return
                } else if (!regEmail.test(this.signUp.email)) {
                    this.$root.$refs.toast.open({
                        theme: "warning",
                        content: "邮箱不合法！"
                    });
                    return
                }

                this.$http.post('/door/register', {
                    name : this.signUp.name,
                    email : this.signUp.email,
                    password : this.signUp.password
                }).then(() => {
                    this.signIn.name = this.signUp.email;
                    this.signIn.password = this.signUp.password;
                    this.login();
                    this.signUp.name = "";
                    this.signUp.email = "";
                    this.signUp.password = "";
                    var vm = this;
                    setTimeout(function () {
                        vm.$router.push({ path: '/door/star' });
                    }, 1000);
                }, (res) => {
                    if (res.status === 422) {
                        if (res.body.email) {
                            this.$root.$refs.toast.open({
                                theme: "warning",
                                content: res.body.email
                            });
                        }
                        if (res.body.name) {
                            this.$root.$refs.toast.open({
                                theme: "warning",
                                content: res.body.name
                            });
                        }
                        if (res.body.password) {
                            this.$root.$refs.toast.open({
                                theme: "warning",
                                content: res.body.password
                            });
                        }
                    } else if (res.status === 429) {
                        this.$root.$refs.toast.open({
                            theme: "error",
                            content: "三分钟内禁止注册！"
                        });
                    } else if (res.status === 500) {
                        this.$root.$refs.toast.open({
                            theme: "error",
                            content: "服务器异常，请稍后再试！"
                        });
                    }
                    return false
                });
            },
            showCaptcha () {
                if (this.signUp.captcha) {
                    this.signUp.captcha = false;
                    var vm = this;
                    var handler = function (captchaObj) {
                        captchaObj.appendTo(vm.$refs.captcha);

                        captchaObj.onSuccess(function () {
                            var promise = new Promise(function(resolve, reject) {
                                if (!vm.register()){
                                    setTimeout(function () {
                                        captchaObj.refresh()
                                    }, 500);
                                }
                            });
                        });
                    };

                    this.$http.get('/door/captcha?rand=' + Math.round(Math.random() * 100)).then((res) => {
                        var temp = JSON.parse(res.body);
                        initGeetest({
                            gt: temp.gt,
                            challenge: temp.challenge,
                            product: "float",
                            offline: !temp.success
                        }, handler);
                    });
                }
            }
        }
    }
</script>


