<style lang="sass" rel="scss" scoped>
    #search {
        border-radius: 16px;
        padding-right: 32px;
        height: 32px;
        overflow: hidden;
        background-color: rgba(0,0,0,.15);
        position: relative;

        &:hover {
            background-color: rgba(0, 0, 0, 0.3);
        }

        input {
            font-size: 12px;
            float: left;
            height: 32px;
            transition: .2s;
            background-color: transparent;
            line-height: 24px;
            padding: 4px 10px 4px 15px;
            border: none;
            border-radius: 0;
            width: 88px;
            color: #fff;

            &:focus {
                background-color: #fff;
                width: 158px;
                color: #222;

                & + button {
                    background-color: #fff;
                    background-image: url(data:image/png;base64,);

                    &:hover {
                        background-image: url(data:image/png;base64,);
                    }
                }
            }
        }

        button {
            width: 32px;
            height: 32px;
            position: absolute;
            top: 0;
            right: 0;
            background-size: 16px;
            background-position: 8px;
            background-repeat: no-repeat;
            border-radius: 0 16px 16px 0;
            background-image: url(data:image/png;base64,);
            transition: .2s;
        }
    }
</style>

<template>
    <div id="search" v-if="!$root.$data.isMobile">
        <input type="text" placeholder="搜索" v-model="q" @keyup.enter="query">
        <button @click="query"></button>
    </div>
</template>

<script>

    export default {
        data () {
            return {
                q : ""
            }
        },
        methods: {
            query () {
                if (this.q.length !== 0) {
                    this.$http.get('/api/query/index', { params : {
                        q : this.q
                    }}).then((res) => {
                        this.$router.push({ path: '/bangumi/' + res.body.data });
                        this.q = "";
                    }, (res) => {
                        if (res.status === 404) {
                            this.$root.$refs.toast.open({
                                theme: "info",
                                content: "没有找到数据！"
                            });
                        } else {
                            this.$root.$refs.toast.open({
                                theme: "error",
                                content: "服务器异常，获取数据失败！"
                            });
                        }
                    });
                }
            }
        }
    }
</script>


