<style lang="sass" rel="scss">
    /* 菜单颜色、上边框颜色 */
    $fore-color: #99a2aa;

    /* 菜单选中状态的颜色 */
    $selected-color: #00a7de;

    /* input focus 时的颜色 */
    $focus-input-color: #00a7de;

    /* 按钮颜色 */
    $button-color: #00a7de;

    /* tab selected 状态下的颜色 */
    $selected-tab-color: #00a7de;

    // ---------- end 全局颜色配置 ------------


    .wangEditor-container {
        position: relative;
        background-color: #fff;
        z-index: 1;
        width: 100%;

        a:focus,
        button:focus{
            outline:none;
        }

        &,* {
            margin: 0;
            padding: 0;
            box-sizing: border-box;
            line-height: 1;
        }

        img {
            border: none;
        }

        .clearfix:after {
            content: '';
            display: table;
            clear: both;
        }
        .clearfix {
            *zoom: 1;
        }

        textarea {
            border: none;
            &:focus{
                outline: none;
            }
        }

        // 显示p head 高度的 tip
        .height-tip {
            position: absolute;
            width: 3px;
            background-color: #ccc;
            left: 0;
            transition: top .2s;
        }

        // 设置 img table 的 toolbar
        .txt-toolbar {
            position: absolute;
            background-color: #fff;
            padding: 3px 5px;
            border-radius: 5px;
            border-top: 2px solid $fore-color;
            box-shadow: 1px 3px 3px #ccd0d7;

            // for IE8
            border-left: 1px\9 solid\9 #ccc\9;
            border-bottom: 1px\9 solid\9 #ccd0d7\9;
            border-right: 1px\9 solid\9 #ccd0d7\9;

            // 小三角
            .tip-triangle {
                display: block;
                position: absolute;
                width: 0;
                height: 0;
                border: 5px solid;
                border-color: transparent transparent $fore-color transparent;
                top: -12px;
                left: 50%;
                margin-left: -5px;
            }

            a {
                color: $fore-color;
                display: inline-block;
                margin: 0 3px;
                padding: 5px;
                text-decoration: none;
                border-radius: 3px;

                &:hover {
                    background-color: #f6f9fa;
                }
            }
        }
        // 图品拖拽大小
        .img-drag-point {
            display: block;
            position: absolute;
            width: 12px;
            height: 12px;
            border-radius: 50%;
            cursor: se-resize;
            background-color: $fore-color;
            margin-left: -6px;
            margin-top: -6px;
            box-shadow: 1px 1px 5px #ccd0d7;
        }

        // 进度条
        .wangEditor-upload-progress {
            position: absolute;
            height: 1px;
            background: #1e88e5;
            width: 0;
            display: none;
            -webkit-transition: width .5s;
            -o-transition: width .5s;
            transition: width .5s;
        }
    }
    .wangEditor-fullscreen {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
    }
    .wangEditor-container {
        .code-textarea {
            resize: none;
            width: 100%;
            font-size: 14px;
            line-height: 1.5;
            font-family: 'Verdana';
            color: #333;
            padding: 0 15px 0 15px;
        }
    }
    .wangEditor-menu-container {

        width: 100%;
        border-bottom: 1px solid #f6f9fa;
        background-color: #fff;

        a {
            text-decoration: none;
        }

        // 菜单组
        .menu-group {
            float: left;
        }

        // 单个菜单容器
        .menu-item {
            float: left;
            position: relative;
            text-align: center;
            height: 31px;
            width: 35px;

            &:hover {
                background-color: rgb(245, 249, 250);
            }

            // 菜单
            a {
                display: block;
                text-align: center;
                color: $fore-color;
                width: 100%;
                padding: 8px 0;
                font-size: 0.9em;
            }

            // 菜单选中状态
            .selected {
                color: $selected-color;
            }

            // 激活状态
            .active {
                background-color: #f6f9fa;
            }

            // 禁用状态
            .disable {
                opacity: 0.5;
                filter: Alpha(opacity=50);
            }
        }

        // tip提示
        .menu-tip {
            display: block;
            position: absolute;
            z-index: 20;
            width: 60px;
            text-align: center;
            background-color: $fore-color;
            color: #fff;
            padding: 7px 0;
            font-size: 12px;
            top: 100%;
            left: 50%;
            margin-left: -30px;
            border-radius: 2px;
            box-shadow: 1px 1px 5px #ccd0d7;

            display: none;

            /*// 小三角
            .tip-triangle {
                display: block;
                position: absolute;
                width: 0;
