<style lang="sass" rel="scss" scoped>
    $search-input-height: 40px;
    $search-input-width: 550px;
    $query-padding: 10px;

    #searchBox {
        width:$search-input-width;
        height:$search-input-height;
        position: absolute;
        left: 50%;
        top: 50%;
        margin-left: -$search-input-width / 2;
        margin-top: -$search-input-height / 2;

        &:hover {
            input {
                border: 1px solid #fff;
                background: rgba(0,0,0,0.3);
            }
        }

        input {
            width: $search-input-width;
            height: 100%;
            margin: 0;
            padding: 0 $query-padding;
            border: 1px solid rgba(255,255,255,.8);
            background: rgba(0,0,0,.15);
            border-radius: 2px;
            color: #fff;
            position: relative;
            font-size: 14px;
            line-height: 40px;
        }

        a {
            display: block;
            width: $search-input-height;
            height: $search-input-height;
            position: absolute;
            right: 0;
            top: 0;
            background: url(data:image/svg+xml;base64,) no-repeat;
            background-position: center;
            background-size: 24px 24px;
        }
    }

    $m-input-height: 40px;
    $m-input-width: 240px;

    #m-search {
        width:$m-input-width;
        height:$m-input-height;
        position: absolute;
        left: 50%;
        top: 50%;
        margin-left: -$m-input-width / 2;
        margin-top: -$m-input-height / 2;

        input {
            width: $m-input-width;
            height: 100%;
            font-size: 13px;
            line-height: 40px;
        }

        a {
            display: block;
            width: $m-input-height;
            height: $m-input-height;
            position: absolute;
            right: 0;
            top: 0;
            background-image: url(data:image/png;base64,);
            background-repeat: no-repeat;
            background-position: center;
            background-size: 20px;
        }
    }
</style>

<template>
    <div>
        <div id="m-search" v-if="$root.$data.isMobile">
            <input autofocus="autofocus" type="text" v-model="q" placeholder="搜索二次元的一切" @keyup.enter="query">
            <a @click="query"></a>
        </div>
        <div id="searchBox" v-else>
            <input autofocus="autofocus" type="text" v-model="q" placeholder="搜索二次元的一切" @keyup.enter="query">
            <a @click="query"></a>
        </div>
    </div>
</template>

<script>

    export default {
        data () {
            return {
                q : ""
            }
        },
        methods: {
            query () {
                if (this.q.length !== 0) {
                    this.$http.get('/api/query/index', { params : {
                        q : this.q
                    }}).then((res) => {
                        this.$router.push({ path: '/bangumi/' + res.body.data });
                    }, (res) => {
                        if (res.status === 404) {
                            this.$root.$refs.toast.open({
                                theme: "info",
                                content: "没有找到数据！"
                            });
                        } else {
                            this.$root.$refs.toast.open({
                                theme: "error",
                                content: "服务器异常，获取数据失败！"
                            });
                        }
                    });
                }
            }
        }
    }

</script>


