<style lang="sass" rel="scss" scoped>
    @import "../../static/sass/variables";

    $music-width: 563px;
    $music-padding: 10px;
    $toggle-width: 22px;
    $music-face-size: 90px;
    $music-height : 115px;

    #music {
        width: $music-width;
        height: $music-height;
        position: fixed;
        bottom: 30px;
        left: -541px;
        background-color: #fff;
        box-shadow: 3px 1px 6px rgba(0,0,0,.25);
        border-radius: 0 4px 4px 0;
        z-index: 1000;
        transition: left .6s ease;
        display: flex;

        .toggle {
            width: $toggle-width;
            height: 100%;
            border-radius: 0 4px 4px 0;
            background-color: $color-blue;
            background-image: url(data:image/png;base64,);
            z-index: -1;
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;
        }

        .M-main {
            padding: $music-padding;
            width: $music-width - $toggle-width;
            height: 100%;
            flex: 1;
            display: flex;
            flex-direction: column;
        }

        .M-face {
            width: $music-height - 2 * $music-padding;
            height: $music-height - 2 * $music-padding;
            margin-right: $music-padding;
        }

        .M-warp {
            height: $music-height - 2 * $music-padding;
            display: flex;
        }

        .M-control {
            flex: 1;
            display: flex;
            flex-direction: column;

            button {
                background-repeat: no-repeat;
                background-size: contain;
            }

            span {
                color: #99a2aa;
                font-size: 12px;
            }

            .center {
                flex: 1;
                display: flex;
                align-items: center;
                justify-content: space-between;
            }

            .bottom {
                text-align: right;
            }
        }

        .M-curBar, .M-allBar {
            display: block;
            position: absolute;
            left: 0;
            bottom: 0;
            height: 4px;
        }

        .M-curBar {
            width: 0;
            background-color: $color-blue;
        }

        .M-allBar {
            width: 100%;
            z-index: 2;
            cursor: pointer;
        }

        .M-tip {
            position: absolute;
            bottom: 12px;
            width: 50px;
            height: 17px;
            line-height: 17px;
            font-size: 10px;
            text-align: center;
            background-color: #fff;
            border: solid 1px $color-gray-hover;
            border-radius: 3px;

            em {
                border-width: 8px;
                border-color: transparent;
                border-top-color: $color-gray-hover;
                border-style: dashed dashed solid;
                position: absolute;
                left: 17px;
                top: 15px;
            }

            i {
                border-width: 6px;
                border-color: transparent;
                border-top-color: #fff;
                border-style: dashed dashed solid;
                position: absolute;
                left: -6px;
                top: -8px;
            }
        }

        .M-play {
            display: inline-block;
            vertical-align: middle;
            width: 120px;
            height: 40px;
            text-align: center;
            margin: 0 70px 0 120px;
        }

        #paused {
            width: 40px;
            height: 40px;
            border-radius: 50%;
        }

        .play-wait {
            background-image: url(data:image/png;base64,);
        }

        .play-go {
            background-image: url(data:image/png;base64,);
        }

        .M-voice-control {
            display: inline-block;
            vertical-align: middle;
        }

        .M-all-voice {
            position: relative;
            display: inline-block;
            width: 70px;
            height: 3px;
            vertical-align: middle;
            background-color: $color-gray-hover;
            cursor: pointer;
        }

        .M-cur-voice {
            position: absolute;
            left: 0;
            top: 0;
            width: 35px;
            height: 3px;
            background-color: $color-blue;
        }

        #muted {
            width: 20px;
            height: 20px;
            vertical-align: middle;
        }

        .voice-go {
            background-image: url(data:image/png;base64,);
        }

        .voice-no {
            background-image: url(data:image/png;base64,);
        }
    }
</style>

<template>
    <div id="music" ref="box">
        <audio ref="audio"></audio>
        <div class="M-main">
            <div class="M-warp">
                <img class="M-face" ref="banner">
                <div class="M-control">
                    <div>
                        <a>{{ source.album }}</a>
                        <a>{{ source.player }}</a>
                    </div>
                    <div class="center">
                        <button></button>
                        <button id="paused" :class="[ status.playing ? 'play-go' : 'play-wait']" ref="paused"></button>
                        <div class="M-voice-control">
                            <button id="muted" :class="[ status.silent ? 'voice-no' : 'voice-go' ]" ref="muted"></button>
                            <span class="M-all-voice" ref="allvoice">
                                <span class="M-cur-voice" ref="curvoice"></span>
                            </span>
                        </div>
                    </div>
                    <div class="bottom">
                        <span>{{ time.cur }} / {{ time.all }}</span>
                    </div>
                </div>
            </div>
            <span class="M-allBar" @mouseout="tipShow = false" ref="alltime">
                <span class="M-curBar" ref="curtime"></span>
            </span>
            <div class="M-tip" v-show="tipShow" ref="tip">{{ tipText }}<em><i></i></em></div>
        </div>
        <button class="toggle" @click="musicToggle"></button>
    </div>
</template>

<script>

    export default {
        props: {
            source : {
                default : null,
                required : true
            }
        },
        data () {
            return {
                open : false,
                timeWidth : 541,
                voiceWidth : 70,
                tipShow : false,
                tipWidth : 25,
                tipText : "",
                init : true,
                status : {
                    playing : false,
                    silent : false
                },
                time : {
                    cur : "00:00",
                    all : "00:00"
                }
            }
        },
        methods: {
            musicToggle () {
                var box = this.$refs.box;

                this.open ? box.style.left = "-541px" : box.style.left = "0px";

                this.open = !this.open;

                if (this.init) {

                    this.init = false;

                    this.$refs.audio.setAttribute('src', this.source.src);

                    this.$refs.banner.setAttribute('src', this.source.img);
                }
            },
            formatSeconds (second) {
                var minute = 0;
                var hour = 0;
                if (second > 60) {
                    minute = parseInt(second / 60);
                    second = (second % 60).toFixed(0);
                    if (minute > 60) {
                        hour = parseInt(minute / 60);
                        minute = parseInt(minute % 60);
                    }
                } else {
                    second = parseInt(second);
                }
                second = second < 10 ? '0' + second : second;
                minute = minute < 10 ? '0' + minute : minute;
                return [hour, minute, second];
            }
        },
        mounted () {
            var vm = this;
            var audio = this.$refs.audio;
            audio.volume = 0.5;

            audio.oncanplay = function () {
                // 缓冲已足够开始时
            };

            audio.oncanplaythrough = function () {
                // 缓冲已完成
            };

            audio.onended = function () {
                // console.log("onended : 音乐播放完毕");
                vm.status.playing = false
            };

            audio.onloadedmetadata = function () {
                // 数据的分辨率或时间长度已加载完成
                var time = vm.formatSeconds(this.duration);
                vm.time.all = time[1] + ":" + time[2];
            };

            audio.onplay = function () {
                // console.log("onplay : 播放开始");
            };

            audio.onwaiting = function () {
                // console.log("onwaiting : 播放过程中开始缓冲");
            };

            audio.ontimeupdate = function () {
                // 当播放位置改变时
                var time = vm.formatSeconds(this.currentTime);
                vm.$refs.curtime.style.width = vm.timeWidth * this.currentTime / this.duration + "px";
                vm.time.cur = time[1] + ":" + time[2];
            };

            audio.onplaying = function () {
                // console.log('onplaying : 播放中...');
            };

            this.$refs.muted.onclick = function () {
                vm.status.silent = !vm.status.silent;
                audio.muted = !audio.muted;
            };


            vm.$refs.paused.onclick = function () {
                vm.status.playing = !vm.status.playing;
                audio.paused ? audio.play() : audio.pause()
            };

//            document.getElementById('loop').onclick = function () {
//                audio.loop = !audio.loop;
//            };

            this.$refs.allvoice.onclick = function (e) {
                audio.volume = e.layerX / vm.voiceWidth;
                vm.$refs.curvoice.style.width = e.layerX + "px";
            };

            this.$refs.alltime.onclick = function (e) {
                vm.$refs.curtime.style.width = e.layerX + "px";
                audio.currentTime = e.layerX / vm.timeWidth * audio.duration;
            };

            this.$refs.alltime.onmousemove = function (e) {
                var time = vm.formatSeconds(audio.duration / vm.timeWidth * e.layerX);
                vm.tipShow = true;
                vm.$refs.tip.style.left = e.layerX - vm.tipWidth + "px";
                vm.tipText = time[1] + ":" + time[2];
            };
        }
    }
</script>


